.filter_tab{
justify-content: center;
align-content: center;
display: flex;
height: 60px;
align-items: center;
}

.filter{
background-image: url('../../../assets/icons/filter.png');
background-position: center;
background-repeat: no-repeat;
background-color: rgb(253, 253, 253);
justify-self:flex-end;
border-radius: 30px;
background-size:70%;
padding: 20px 20px;
 
}

.filters{
    
 align-self: center;
justify-self: center;
height: 40px;
padding: 5px;
}


.header{
    justify-self: center;
    text-align: center;
}
.select{
        background-color: #ffffff;
        color: rgb(0, 0, 0);
     margin: 10px 0;
     height: 30px;
     width:fit-content;
        text-align: center;
        border-radius: 5px;
        margin-left: 15px;
        font-size: 20px;
        box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
        -webkit-appearance: button;
        font-weight: bold;
        appearance: button;
        outline: none;
       
}



.filter_btn {
   display: flex;
   flex-direction: row;
 }

.filter_btn button{
    height: 50px;
    width: 100px;
   align-items: baseline;
   font-size: 1.3rem;
    margin:0 15px;
    color: rgba(57, 101, 125, 0.8274509804);
   background-color: rgba(255, 255, 255, 0.802);
    border: 2px solid rgba(57, 101, 125, 0.8274509804);
  

}

.filter_btn button::before{
content: '';
display:inline-block;
color: rgb(105, 105, 105);
width: 20px;
height: 15px;
background-image: url('../../../assets/icons/filter.png');
background-position: center;
background-size: contain;
background-repeat: no-repeat;


 }