body {
  position: relative;
  color:rgba(57, 101, 125, 0.8274509804);
  width:auto;
  background:rgb(247, 245, 245) ;
  font-family: sans-serif;
 margin:0;
display: flex;
flex-direction: column;
justify-content: center;
align-items:center;

 
}

.inputs{
  display: flex;
  flex-direction: column;
  align-items: center;
  
}



.files-arr{
min-width: fit-content;
margin-right: 15px;
font-size:0.9em;
color: #39657dd3;
}

.logo{
  background-image: url('/assets/LOGO_final_for_print_blue.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width:300px ;
  height:200px;
  margin: 1%;
}

.logo-1{
  background-image: url('/assets/imges/logo.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width:100%;
  height:200px;
  margin: 20% 0;
}


.form{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}

.file-input{
font-size: x-large;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
margin:0 0 5% 0;
}


.file-select {
	display: block;
	color: #dbdbdb;
	cursor: pointer;
	text-align: left;
	background: #ffffff;
  width: 600px;
	overflow: hidden;
	position: relative;
	border-radius: 6px;
}

.file-select .file-select-button {
	background: #273e41b2;
	padding: 10px;
 
	display: inline-block;
}


.file-select .file-select-name {
	display: inline-block;
	padding: 10px;
  font-size: large;
}
 .file-select:hover .file-select-button {
	background: #7cddecd4;
	color: #ffffff;
	transition: all 0.2s ease-in-out;
	-moz-transition: all 0.2s ease-in-out;
	-webkit-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
}
 .file-select input[type="file"] {
	display: none;
}

.form-container{
  display: flex;
  justify-content: center;
 flex-direction: column;
 align-items: center;
  
}

.switch-btn{
  margin-top: 15px;
  background-color: rgb(81, 153, 247);
  color: #ececec;
  border-radius: 30px;
}

.analyze-btn{
  margin-top: 1%;
  border-radius: 50px;
  width: 250px;
  height: 70px;
  font-size: x-large;
  background-color: rgba(243, 243, 243, 0.397);
  transition: all ease-in 0.1s;
}

.analyze-btn:hover {
  border:2px solid #7cddecb2 ;
  border-top:2px solid #dd2c2cb2 ;
  border-bottom:2px solid #DD2C2cb2 ;
  animation: rotate-center 0.7s ease-in-out infinite ;
}

.analyze-btn:disabled {
pointer-events: none;
 
  background-color: rgba(239, 239, 239, 0.151);
  color: rgba(16, 16, 16, 0.3) ;
  border-color: rgba(118, 118, 118, 0.3) ;
  border: 1px solid;
}

@keyframes rotate-center {
  0% {
    border:2px solid #7cddecb2 ;
  border-top:2px solid #dd2c2cb2 ;
  border-bottom:2px solid #DD2C2cb2 ;
  }
  50%{
    border:2px solid #DD2C2cb2;
  border-top:2px solid #7cddecb2  ;
  border-bottom:2px solid #7cddecb2  ;
  }
  100% {
    border:2px solid #7cddecb2 ;
    border-top:2px solid #dd2c2cb2 ;
    border-bottom:2px solid #DD2C2cb2 ;
  }
}


button {
  color: rgb(105, 105, 105);
  padding: 8px 7px;
  border-radius: 10px;
  border: none;
  appearance: none;
  font-size: 1.3rem;
  box-shadow: 0px 8px 28px -6px rgba(24, 39, 75, 0.12),
    0px 18px 88px -4px rgba(24, 39, 75, 0.14);
  transition: all ease-in 0.1s;
  cursor: pointer;
  opacity: 0.9;
}

button:hover {
  transform: scale(1.05);
  opacity: 1;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  height: fit-content;
  width: fit-content;
  margin: 10px;
}

a:hover {
  opacity: 1;
  text-decoration: none;
}

.dash {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
 width: 90vw;
}


.high-pack{
  border: 3px solid transparent;
border-left-color: orange;
border-right-color: orange;
}

.danger-pack{
  border: 3px solid transparent;
  border-left-color: rgb(233, 56, 56);
  border-right-color: rgb(233, 56, 56);
}

.medium-pack{
border: 3px solid transparent;
border-left-color: yellow;
border-right-color: yellow;


 
}

.low-pack{
border: 3px solid transparent;
border-left-color:lightgreen;
border-right-color:lightgreen;
}



.all-data{
width: 100%;
margin-bottom: 20%;
}


.container{
  width: 100%; 
  background-color: #D9D9D9;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content:space-between;
  align-content: center;
  align-items: center;
}

.ver{
 color: lightgray;
  margin-top: 3%;
  justify-self: center;
  align-self: center;
  text-align: center;
}


.pies{
  background-color: white;
width: 95%;
  display: flex;
  flex-direction: row;
  
 
}

@media (max-width :1450px ) {
  .pies{
   flex-direction: column;
    
  } 
}


.header{
  color: white;
  width: 100%;
  padding: 2% 0;
  font-size: x-large;
  justify-self: flex-start;
  background-color: #7cddecb2;
  text-align: center;
}

.header-top{
  color: white;
  width: 100%;
  padding: 7.5% 0;
  justify-self: flex-start;
  background-color: #7cddecb2;
  text-align: center;
}

.bold{
  font-weight: bold;
  color: #39657dd3;
}


