.logo{
    background-image: url('/assets/imges/logo.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width :400px;
    height:200px;
    margin: 20% 0;
    align-self: center;
    justify-self: center;
    position: absolute;
    z-index: 4;
  }

  .logo_container{
    content: '';
    margin: 13% 0;
    height: 400px;
    border: 20px solid transparent ;
    width: 400px;
    border-top-color:#DD2C2C ;
    border-bottom-color:#7CDEEC ;
    
    border-radius: 100%;
    align-self: center;
    justify-self: center;
    z-index: 4;
    animation: loading 3s ease-out infinite;
  }

  @keyframes loading{
    to{
        transform: rotate(1turn);
    }
  }

  .model{
    display:  flex !important; 
    z-index: 3;
    width: 100%;
    height: 100%;
    align-items: center;
    background: rgba(240, 240, 240, 0.7);
    bottom: 0;
    display: none; 
    justify-content: center;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
   
  }